.h5vp_licence_popup {
  display: none;
}
#h5vp.popupWrapper {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

#h5vp .overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  background: #000;
  opacity: 0.7;
}
#h5vp .licence_form {
  position: relative;
  width: 550px;
  margin: 0 auto;
  top: 20%;
  // padding: 30px;
  text-align: center;
  z-index: 9999999;
}
#h5vp .popup_header {
  height: 50px;
  align-items: center;
  display: flex;
  // justify-content: center;
  background: #fbfbfb;
  padding: 0 20px;
}
#h5vp .popup_body {
  min-height: 120px;
  overflow: hidden;
  background: #ffffff;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #dddddd;
  padding: 20px;
}
#h5vp .popup_footer {
  height: 50px;
  align-items: center;
  display: flex;
  justify-content: flex-end;
  background: #fbfbfb;
  padding: 0 20px;
}
#h5vp .licence_form h2 {
  font-size: 25px;
  text-align: center;
}
#h5vp .licence_form input[type="text"] {
  width: 500px;
  margin: 0 auto;
}
.h5vp_licence_notice {
  margin-top: 15px;
}
.h5vp_licence_notice .notice {
  padding: 5px;
}
// .licence_form input[type="submit"] {
//   padding: 10px 20px;
//   text-align: center;
// }
#h5vp span.h5vp_closer {
  position: absolute;
  right: 10px;
  top: 10px;
  background: #ff00ff;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}
#h5vp .terms {
  display: flex;
  text-align: left;
  margin-top: 15px;
  margin-bottom: 15px;
}
#h5vp .terms > input {
  margin-right: 10px;
  margin-top: 4px;
}
#h5vp .terms > p {
  margin: 0;
}
