
.bplugins-meta-readonly {
  /* pointer-events: none; */
  opacity: 0.6;
}
.csf-field.bplugins-meta-readonly:hover::after {
  display: block;
}

.csf-field.bplugins-meta-readonly::before {
  display: block;
  width: 100%;
  height: 100%;
  content: "";
  position: absolute;
  z-index: 999;
  overflow: hidden;
  top: 0;
  left: 0;
}

