  display: none;
  content: "The option is available in the pro version only";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999999;
  font-size: 22px;
  background: #673ab7;
  color: #fff;
  padding: 10px 13px;
  border-radius: 3px;
}

