.h5vp_front_shortcode input {
  cursor: pointer;
  text-align: center;
  border: none;
  outline: none;
  background-color: rgb(30, 140, 190);
  color: rgb(255, 255, 255);
  padding: 4px 10px;
  border-radius: 3px;
}
.h5vp_front_shortcode {
  position: relative;
  display: inline-block;
  margin: 0 auto;
}

.h5vp_front_shortcode .htooltip {
  visibility: hidden;
  width: 140px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 150%;
  left: 50%;
  margin-left: -75px;
  opacity: 0;
  transition: opacity 0.3s;
}

.h5vp_front_shortcode .htooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.h5vp_front_shortcode:hover .htooltip {
  visibility: visible;
  opacity: 1;
}
