//analytics
.h5vp-analytics {
  .chart .chart {
    width: 98%;
  }
  .chart-user {
    display: flex;
    .chart {
      width: 65%;
    }
    .user {
      width: calc(31% - 40px);
      margin-left: 2%;
      background: #fff;
      padding: 10px 20px 20px 20px;
      h3 {
        margin-bottom: 25px;
        font-size: 20px;
      }
      table {
        width: 98%;
        text-align: left;
        background: #ffffff;
        padding: 15px;
        border-collapse: collapse;
        thead tr {
          background: #e6e6e6;
        }
        tbody tr:hover {
          background: #e6e6e6;
        }
        tr {
          border-bottom: 1px solid #ddd;
          cursor: pointer;
          td {
            padding: 7px 15px;
            font-size: 16px;
            cursor: pointer;
          }
          th {
            padding: 7px 15px;
            font-size: 16px;
          }
        }
      }
    }
  }
  .top-videos {
    margin-top: 50px;
    width: 98%;
    background: #fff;
    padding: 10px 50px 50px;
    box-sizing: border-box;
    @media screen and (max-width: 1366px) {
      padding: 10px 20px 30px;
    }
    h3 {
      font-size: 22px;
      margin-bottom: 35px;
    }
    table {
      width: 98%;
      text-align: left;
      background: #ffffff;
      padding: 15px;
      border-collapse: collapse;
      thead tr {
        background: #e6e6e6;
      }
      tbody tr:hover {
        background: #e6e6e6;
      }
      tr {
        border-bottom: 1px solid #ddd;
        cursor: pointer;
        td {
          padding: 10px 15px;
          font-size: 18px;
          cursor: pointer;
        }
        th {
          padding: 10px 15px;
          font-size: 18px;
        }
      }
    }
  }
  .user-boxes {
    display: flex;
    width: 98%;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 10px;
    box-sizing: border-box;
    @media screen and (max-width: 767px) {
      flex-direction: column;
      align-items: center;
    }
    .user-box {
      background: #fff;
      padding: 20px;
      text-align: center;
      box-sizing: border-box;
      width: calc(33% - 10px);
      @media screen and (max-width: 767px) {
        width: 100%;
      }
    }
  }
  .back_to_dashboard {
    padding: 10px 20px;
    margin-bottom: 20px;
    display: inline-block;
    text-align: center;
    margin-left: 0;
    margin-top: 20px;
    text-decoration: none;
    font-size: 16px;
    border-radius: 5px;
    color: #fff;
    background: #4527a4;
    span {
      margin-right: 5px;
    }
    &:hover {
      background: #7043fc;
    }
  }
}
